<template>
    <yd-layout class="approveForm">
        <div class="processForm">
            <div class="mainContentFluid selectList" v-if="fuyanFlag">
                <div class="fluidWidth fluidWidth2">
                    <yd-cell-group>
                        <yd-cell-item>
                            <span slot="left" class="lastPost">上一则附言：</span>
                            <div class="fyColor" slot="right" v-text="fuyan"></div>
                        </yd-cell-item>
                    </yd-cell-group>
                </div>
            </div>
            <div class="top">
                <div class="header">
                    <div class="title">{{jsonData.reqname}}</div>
                    <div class="approveType">{{jsonData.approvalitem}}</div>
                    <i class="circle01"></i>
                    <i class="circle02"></i>
                </div>
                <div class="partClo partCloSpe" v-html="transferString(jsonData.reqdesc)">
                </div>
                <div class="empInfo empInfo1">
                    <div class="infoLeft">发起方：</div>
                    <div class="infoRight" v-text="jsonData.reqdeptname"></div>
                </div>
                <div class="empInfo empInfo2">
                    <div class="infoLeft">联系人：</div>
                    <div class="infoRight" v-text="jsonData.requsername"></div>
                    <div class="infoPhone" v-text="jsonData.reqtel"></div>
                    <i class="circle01"></i>
                    <i class="circle02"></i>
                </div>
                <div class="remark" v-if="jsonData.reqremark!=undefined">
                    <div class="remarktitle">
                        备注：
                    </div>
                    <div class="remarkTxt" v-html="jsonData.reqremark">
                    </div>
                </div>
            </div>
            <div class="bottom" v-if="aproveList!=''">
                <div class="botTitle">
                    审批记录
                </div>
                <div class="record" v-for="(item, index) in aproveList" :key="index">
                    <div class="nameSugg">
                        <div class="name">{{item.partiname}}：</div>
                        <div class="suggest" v-text="item.neirong"></div>
                    </div>
                    <div class="recorddate" v-text="item.submitDate">
                    </div>
                    <!-- <div class="recorddate myfq">
                        我发起的流程
                    </div> -->
                </div>
            </div>
        </div>
        <yd-popup v-model="show" position="center" width="5.79rem">
            <div class="other">意见</div>
            <yd-cell-group class="otherInput">
                <yd-cell-item>
                    <yd-textarea slot="right" v-model="opinion" placeholder="请您输入意见..."></yd-textarea>
                </yd-cell-item>
            </yd-cell-group>
            <div class="otherBtn">
                <ul>
                    <li class="yes" @click="approveBtn()">确认</li>
                    <li class="no" @click="show=false">取消</li>
                </ul>
            </div>
        </yd-popup>
        <yd-tabbar slot="tabbar" v-if="$route.params.page==1&&page!=4&&status=='0'">
            <div class="agree" @click="$router.push('/incCl/'+$route.params.workId)">送领导审批</div>
            <div class="back">送人力资源审批</div>
        </yd-tabbar>
        <yd-tabbar slot="tabbar" v-if="$route.params.page==2&&page!=4&&status=='0'">
            <div class="agree" @click="opinion='请根据公司规定据实开具。';approveresult='1';show=true">同意</div>
            <div class="back disagree" @click="opinion='请更新需求，重新申请。';approveresult='0';show=true">不同意</div>
            <!-- <div class="otheragree" @click="show=true">其他意见</div> -->
        </yd-tabbar>
        <yd-tabbar slot="tabbar" v-if="$route.params.page==4&&status=='0'">
            <div class="agree" @click="finishPro()">正常结束</div>
        </yd-tabbar>
        <yd-tabbar slot="tabbar" v-if="$route.params.page==3&&page!=4&&status=='0'">
            <div class="agree" @click="$router.push('/incCl/'+$route.params.workId)">送领导审批</div>
            <div class="back" @click="$router.push('/incAccept/'+$route.params.workId)">发送</div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex';
export default {
    name: 'approveForm',
    data() {
        return {
            show: false,
            jsonData: {},
            aproveList: [],
            nextKey: '',
            approveType: '',
            approveresult: '',
            opinion: '',
            leaderIDs: '',
            queryHandlerPerson: [23, 24, 25],
            approver: [],
            secretariesList: [],
            fuyanFlag: false,
            fuyan: '',
            page: this.$route.params.page,
            status: this.$route.params.status
        }
    },
    mounted() {
        document.setTitle('快捷审批流程');
        let vm = this;
        vm.formData();
    },
    watch: {
        approver: function() {
            let vm = this;
            vm.setIncomeInfo({
                approver: vm.approver
            })
        }
    },
    computed: {
        ...mapGetters(['getUserInfo', 'getIncomeInfo'])
    },
    methods: {
        ...mapMutations([
            'setIncomeInfo'
        ]),
        formData() {
            let vm = this;
            if (vm.getIncomeInfo.jsonData != '' && vm.getIncomeInfo.jsonData.workId == vm.$route.params.workId) {
                vm.jsonData = vm.getIncomeInfo.jsonData;
                for (let key in vm.jsonData.nextBtn) {
                    if (key == 'doApprove') {
                        vm.nextKey = key;
                        for (let keyJ in vm.jsonData.nextBtn.doApprove) {
                            if (keyJ == 'type') {
                                vm.approveType = vm.jsonData.nextBtn.doApprove.type;
                            } else {
                                vm.leaderIDs = keyJ;
                            }
                        }
                    }
                }
                if (vm.jsonData.CURRENTSTATE != 10 && vm.$route.params.status == 0) {
                    vm.$dialog.toast({
                        mes: '该流程已被处理',
                        timeout: 2000,
                        callback: () => {
                            setTimeout(function() {
                                if (vm.$route.params.status == 0) {
                                    vm.$router.push('/handle');
                                }
                            })
                        }
                    });
                    if (vm.$route.params.status == 0) {
                        return false;
                    }
                }
            } else {
                vm.queryForm();
            }
            if ((vm.getIncomeInfo.aproveList === '' || vm.getIncomeInfo.aproveList.length != 0) && vm.getIncomeInfo.jsonData.workId == vm.$route.params.workId) {
                vm.aproveList = vm.getIncomeInfo.aproveList;
            } else {
                vm.approveLog();
            }
            if ((vm.getIncomeInfo.fuyan === '' || vm.getIncomeInfo.fuyan.length != 0) && vm.getIncomeInfo.jsonData.workId == vm.$route.params.workId) {
                vm.fuyan = vm.getIncomeInfo.fuyan;
            } else {
                vm.getLast();
            }
            if (vm.getIncomeInfo.approver.length != 0 && vm.getIncomeInfo.jsonData.workId == vm.$route.params.workId) {
                vm.approver = vm.getIncomeInfo.approver;
            } else if (vm.$route.params.page == 3 || vm.$route.params.page == 1) {
                vm.queryLeader();
            }
            if (vm.getIncomeInfo.secretariesList.length != 0 && vm.getIncomeInfo.jsonData.workId == vm.$route.params.workId) {
                vm.secretariesList = vm.getIncomeInfo.secretariesList;
            } else {
                vm.getSecretaries();
            }
        },
        queryForm() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryForm', {
                'type': 'crossSysSimAppr',
                'workItemID': vm.$route.params.workId,
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`
            }, function(res) {
                if (res.jsonData.CURRENTSTATE != 10 && vm.$route.params.status == 0 && res.isSuccess == 'true') {
                    vm.$dialog.toast({
                        mes: '该流程已被处理',
                        timeout: 2000,
                        callback: () => {
                            setTimeout(function() {
                                if (vm.$route.params.status == 0) {
                                    vm.$router.push('/handle');
                                }
                            })
                        }
                    });
                    if (vm.$route.params.status == 0) {
                        return false;
                    }
                }
                vm.jsonData = res.jsonData;
                res.jsonData.workId = vm.$route.params.workId;
                vm.setIncomeInfo({
                    jsonData: res.jsonData
                });
                for (let key in vm.jsonData.nextBtn) {
                    if (key == 'doApprove') {
                        vm.nextKey = key;
                        for (let keyJ in vm.jsonData.nextBtn.doApprove) {
                            if (keyJ == 'type') {
                                vm.approveType = vm.jsonData.nextBtn.doApprove.type;
                            } else {
                                vm.leaderIDs = keyJ;
                            }
                        }
                    }
                }
            });
        },
        queryLeader() {
            let vm = this;
            vm.queryHandlerPerson.map((item, index) => {
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/handlerPerson', {
                    'type': 'crossSysSimAppr',
                    'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    'queryType': item
                }, function(res) {
                    vm.approver = [...vm.approver, ...res.jsonData];
                }, function(res) {}, false);
            })
        },
        // 获取秘书
        getSecretaries() {
            var vm = this;
            vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/handlerPerson', {
                'type': 'crossSysSimAppr',
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                'queryType': 7
            }, function(res) {
                vm.secretariesList = res.jsonData;
                vm.setIncomeInfo({
                    secretariesList: vm.secretariesList
                })
            }, function(res) {}, false);
        },
        approveLog() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryApprove', {
                'type': 'crossSysSimAppr',
                'workItemID': vm.$route.params.workId,
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`
            }, function(res) {
                // 审批流程
                vm.aproveList = res.jsonData;
                vm.setIncomeInfo({
                    aproveList: res.jsonData
                });
            })
        },
        approveBtn() {
            let vm = this;
            if (vm.nextKey == 'doApprove') {
                if (vm.show == true && vm.opinion == '') {
                    vm.$dialog.toast({
                        mes: '请您输入意见',
                        timeout: 2000
                    });
                } else {
                    vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doApprove', {
                        'type': 'crossSysSimAppr',
                        'approveType': vm.approveType,
                        'workItemID': vm.$route.params.workId,
                        'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                        'leaderIDs': vm.leaderIDs,
                        'fuyan': '',
                        'opinion': vm.opinion,
                        'workTitle': vm.jsonData.reqdeptname + vm.jsonData.requsername + '收入证明申请',
                        'ext': '{"approveresult":"' + vm.approveresult + '"}'
                    }, function(res) {
                        vm.$dialog.toast({
                            mes: '审批完成',
                            timeout: 2000,
                            callback: () => {
                                vm.$router.push('/handle');
                            }
                        });
                    });
                }
            }
        },
        // 送人力
        sendHuman() {
            let vm = this;
            vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                'type': 'crossSysSimAppr',
                'workItemID': vm.$route.params.workId,
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                'activityID': 'simSendReqActivity',
                'leaderIDs': vm.leaderIDs,
                'fuyan': '',
                'opinion': '',
                'workTitle': vm.jsonData.reqdeptname + vm.jsonData.requsername + '收入证明申请',
                'ext': ''
            }, function(res) {
                vm.$dialog.toast({
                    mes: '发送成功',
                    timeout: 2000,
                    callback: () => {
                        vm.$router.push('/handle');
                    }
                });
            })
        },
        finishPro() {
            let vm = this;
            vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                'type': 'crossSysSimAppr',
                'workItemID': vm.$route.params.workId,
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                'activityID': 'finishActivity',
                'leaderIDs': '',
                'fuyan': '',
                'opinion': '',
                'workTitle': vm.jsonData.reqdeptname + vm.jsonData.requsername + '收入证明申请',
                'ext': ''
            }, function(res) {
                if (res.isSuccess == 'true') {
                    vm.$dialog.toast({
                        mes: '发送成功',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle');
                        }
                    });
                }
            })
        },
        // 上一则附言
        getLast() {
            let vm = this;
            vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/oapreviousfuyan', {
                'type': 'crossSysSimAppr',
                'workItemID': vm.$route.params.workId,
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`
            }, function(res) {
                vm.setIncomeInfo({
                    fuyan: res.jsonData
                });
                if (res.jsonData != '') {
                    vm.fuyanFlag = true;
                    vm.fuyan = res.jsonData[0].neirong;
                }
            }, function(res) {}, false);
        }
    }
}
</script>
<style lang="less" scoped>
@import 'proofOfIncome.less';

.yd-cell-item {
    padding-left: 0;
}
.other {
    position: relative;
}
.circle01 {
    height: .2rem;
    width: .2rem;
    background-color: #f1f1f1;
    position: absolute;
    bottom: -0.1rem;
    left: -0.1rem;
    border-radius: 50%;
    z-index: 2
}

.circle02 {
    height: .2rem;
    width: .2rem;
    background-color: #f1f1f1;
    position: absolute;
    bottom: -0.1rem;
    right: -0.1rem;
    border-radius: 50%;
    z-index: 2
}

.selectList {
    margin-top: 0;
}

.fluidWidth2 {
    padding: 0 .45rem;
    padding-top: 0;
    padding-bottom: 0;
}

.fyColor {
    width: 100%;
    text-align: left;
    color: #999;
    font-size: 0.32rem;
    word-break: break-word;
}

.lastPost {
    font-size: 0.32rem;
}

.otherInput {
    padding-left: .2rem;
    border-bottom: 1px solid #e3e3e3;
}
</style>
<style lang="less">
.approveForm {
    .yd-cell:after {
        background-image: none;
    }
}

.processForm {
    .yd-cell-left {
        align-items: flex-start;
        padding-top: .4rem;
    }

    .yd-cell-right {
        align-items: flex-start;
        padding-top: .4rem;
    }

    .yd-cell:after {
        background-image: none;
    }
}
</style>


